* {
  box-sizing: border-box;
}

.form-wrap {
  width: 100%;
  z-index: 100;
  background-color: #0ff;
  padding: 8px;
  position: -webkit-sticky;
  position: sticky;
  box-shadow: 0 1px 3px rgba(4, 91, 114, .12), 0 1px 1px rgba(3, 137, 163, .14), 0 2px 1px rgba(241, 241, 241, .2);
}

.search-form {
  width: 100%;
  justify-content: center;
  display: flex;
}

.searchInput {
  width: 30%;
  height: 36px;
  color: #11252e;
  border: 3px solid #00b4cc;
  border-right: none;
  border-radius: 5px 0 0 5px;
  outline: none;
  padding: 5px;
  font-size: 20px;
}

.searchBtn {
  width: 45px;
  height: 36px;
  cursor: pointer;
  background-color: #00b4cc;
  border: 4px solid #00b4cc;
  border-radius: 0 5px 5px 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.searchBtn:hover, .searchBtn:focus {
  background-color: #13c5dc;
  border-color: #13c5dc;
}

